import React from 'react';
import { graphql, Link } from 'gatsby';
import Layout from '../components/layout';
import Img from 'gatsby-image';
import {kebabCase} from "lodash";
import Fade from 'react-reveal/Fade';
import { ThemeToggler } from 'gatsby-plugin-dark-mode';
import Switch from "react-switch";

const BlogPage = ({data}) => {
    const posts = data.allMarkdownRemark.edges;

    return (
        <ThemeToggler>
            {({ theme, toggleTheme }) => (
        <Layout >
            <h1 className="blogHeader">
                <span onClick={e => window.location.replace("/")} >Aman Dave</span>
                <div style={{ float: "right", fontSize: "20px", paddingTop: "10px"}}>
                    <label>
                        <Switch
                            checked={theme === 'dark'}
                            className='dark-theme'
                            onChange={e => toggleTheme(e ? 'dark' : 'light')}
                            value={theme === 'dark'}
                            onColor="#ffc663"
                            onHandleColor="#eb8621"
                            handleDiameter={33}
                            uncheckedIcon={<img src={require('../images/moon.png')} height="20px" style={{ marginLeft: "7px"}}  />}
                            checkedIcon={<img src={require('../images/sun.png')} height="20px"  style={{ marginLeft: "5px"}}/>}
                            boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
                            activeBoxShadow="0px 0px 1px 10px rgba(0, 0, 0, 0.2)"
                            height={26}
                            width={60}
                            className="react-switch"
                            id="material-switch"
                        />
                    </label>
                </div>
            </h1>

            <br/>
            <Fade right>
                <div
                    style={{
                        backgroundColor: 'var(--bg)',
                        color: 'var(--textNormal)',
                        transition: 'color 0.2s ease-out, background 0.2s ease-out',
                    }}
                    className="post-list">
                    {posts.map(post => {

                        return (post.node.frontmatter.title!=='Resume')?
                        <div key={post.node.id} className="post-list__item" style={{
                            border: "1px solid #393e46", marginBottom: "50px"
                        }}>
                            <div className="post-list__thumbnail">
                                <Link to={post.node.fields.slug}>
                                    <Img
                                        fixed={post.node.frontmatter.thumbnail.childImageSharp.fixed}
                                    />
                                </Link>
                            </div>
                            <div className="post-list__content">
                                <Link to={post.node.fields.slug}>
                                    <h2 className="blogTitle">{post.node.frontmatter.title}</h2>
                                </Link>
                                {post.node.frontmatter.tags ? (
                                    <div className="tags-container">
                                        <ul className="taglist">
                                            {post.node.frontmatter.tags.map(tag => (
                                                <li key={tag + `tag`}>
                                                    <Link to={`/tags/${kebabCase(tag)}/`}>{tag}</Link>

                                                </li>
                                            ))}
                                        </ul>
                                    </div>
                                ) : null}

                                <p>{post.node.frontmatter.date}</p>
                                <div className="post-list__excerpt">{post.node.excerpt}</div>

                            </div>
                        </div>
                            :<div>
                            </div>
                        })}
                </div>
            </Fade>
        </Layout>
            )}
        </ThemeToggler>
    );
}
export default BlogPage;

// Get all markdown data, in descending order by date, and grab the id, excerpt, slug, date, and title
//blog.js
export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            tags
            thumbnail {
              childImageSharp {
                fixed(width: 200, height: 200) {
                  ...GatsbyImageSharpFixed
                }
              }
            }
          }
        }
      }
    }
  }
`;
